<template>
  <div>
    <div class="accordion-item">
      <h2 class="accordion-header" :id="'heading' + domainId">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          :data-bs-target="'#collapse' + domainId"
          aria-expanded="false"
          :aria-controls="'collapse' + domainId"
        >
          {{ domainName }}
        </button>
      </h2>
      <div
        :id="'collapse' + domainId"
        class="accordion-collapse collapse"
        :aria-labelledby="'heading' + domainId"
        :data-bs-parent="'#'+accordId"
        style="text-align: left; padding: 1em"
      >
        <strong>{{ domainData["Summary"] }}</strong>
        <p>{{ domainData["Description"] }}</p>

        <roll-table
          :ChanceArray="domainData['Features']"
          TableName="Features"
          :StartValue="fstartnum"
        />

        <roll-table 
          :ChanceArray="domainData['Dangers']" 
          TableName="Dangers" 
          :StartValue="dstartnum"
          />
      </div>
    </div>
  </div>
</template>

<script>
import RollTable from "./RollTable.vue";
export default {
  components: { RollTable },
  name: "domain",
  props: ["domain-name", "domain-data", "accord-id", 'f-start', 'd-start'],
  computed: {
    domainId() {
      return this.$props.domainName.replace(/\s+/g, "").replace(/[()]/g, "");
    },
    dstartnum() {
      return parseInt(this.$props['dStart']);
    },
    fstartnum() {
      return parseInt(this.$props['fStart']);
    }
  },
  methods: {
    
  }
};
</script>
